// Selections

let form = document.querySelector(".landing_form");
let emailInput = document.querySelector(".landing_newsletter-input");
let submitBtn = document.querySelector(".landing_newsletter-submit");
let submitValue = document.querySelector(".landing_newsletter-input");
let errorIcon = document.querySelector(".error_icon");
let errorTxt = document.querySelector(".error_text");
let successTxt = document.querySelector(".success_text");
let mContainer = document.querySelector(".message_container");
let timer;

// Functions

submitBtn.addEventListener("click", e => {
  e.preventDefault();
  let mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
  if (submitValue.value.match(mailformat)) {
    mContainer.innerHTML = `<p class="success_text show_error">Thank you for subscribing!</p>`;
    emailInput.classList.add("landing_newsletter-input_success");
    clearTimeout(timer);
    timer = setTimeout(removeMessage, 4000);
  }
  if (!submitValue.value.match(mailformat)) {
    mContainer.innerHTML = `<p class="error_text show_error">Please provide a valid email account</p>`;
    errorIcon.classList.add("show_error");
    errorIcon.classList.add("animate__animated");
    errorIcon.classList.toggle("animate__headShake");
    emailInput.classList.add("landing_newsletter-input_error");
    clearTimeout(timer);
    timer = setTimeout(removeMessage, 4000);
  }
  let sValue = submitValue.value;
  if (sValue == null || sValue == "") {
    mContainer.innerHTML = `<p class="error_text show_error">Please enter an email account</p>`;
    clearTimeout(timer);
    timer = setTimeout(removeMessage, 4000);
  }
});

function removeMessage() {
  mContainer.innerHTML = ``;
  errorIcon.classList.remove("show_error");
  emailInput.classList.remove("landing_newsletter-input_error");
  emailInput.classList.remove("landing_newsletter-input_success");
}
